<div class="hello"><div class="ui row" v-if="!uid"><div class="sixteen wide column"> <div class="ui huge buttons"><button class="ui orange button" @click="toggleLogin"><i class="lock icon"></i>{{ $t('login.login_to_see_data') }}</button></div></div></div><div class="ui container" v-if="users &amp;&amp; toList(users).length &gt; 0"><div class="ui divided list flex-column-reverse"><div class="item form" v-show="uid &amp;&amp; users[uid]"><br><br><h4>{{ $t('groups.create_group') }}</h4><div class="field"><div class="ui labeled input"><label class="ui label">{{ $t('groups.group_name') }}</label><input type="text" v-model="newName" @input="filterInput('newName', $event)" :placeholder="$t('groups.enter_group_name')"></div></div><div class="field button-field"><a class="ui green button" :class="{disabled: !newName}" @click="addGroup()">{{ $t('groups.create_group') }}</a></div></div><div class="item" v-for="(g, idx) in searchBy(groups, mySearch)" :key="g.idx" v-show="!g.hidden"><h3>〈{{g.n}}〉</h3><p>{{g.intro}}<br class="thin-only">&nbsp;&nbsp;&nbsp;&nbsp;</p><div class="popular-resources" v-if="getTopResources(g.res).length &gt; 0"><h4>{{ $t('groups.popular_resources') }}(
Total:<i class="book icon"></i>{{ (g.res || []).length }} {{ $t('groups.resources') }}
)</h4><div class="ui bulleted list"><a class="resource-link" v-for="resource in getTopResources(g.res)" :key="resource.href" :href="resource.href" target="_blank"><img class="favicon" :src="getFavicon(resource.href)"><span>{{ resource.n }}</span><span class="likes">({{ (resource.likes || []).length }}</span><i class="red heart icon"></i>)</a></div></div><p> <span style="position: relative; top: 0.6em;">{{$t('groups.members')}}</span><span v-for="m in g.members"><router-link :to="'/flag/' + m" v-if="isUser(m)"><img class="ui avatar" :src="users[m].photoURL" :alt="users[m].name"></router-link></span></p><p><div class="ui buttons"><router-link class="ui basic green button" :to="'/group/' + g.idx"><i class="sign-in icon"></i>{{$t('groups.go_group')}}</router-link><a class="ui green button" v-if="isUser(uid) &amp;&amp; !isMember(g.idx)" @click="join(g.idx)">{{$t('groups.join_group')}}</a><a class="ui red basic button" v-if="isUser(uid) &amp;&amp; isMember(g.idx)" @click="out(g.idx)">{{ $t('groups.out_group') }}</a></div></p></div></div></div></div>