<div class="hello"><div class="ui center aligned grid"><div class="sixteen wide column" v-if="!uid &amp;&amp; (!users || toList(users).length == 0)"><div class="ui huge buttons center aligned"><button class="ui orange button" @click="toggleLogin"><i class="google icon"></i>{{ $t('login.login_to_see_data') }}</button></div></div><div v-if="users &amp;&amp; toList(users).length &gt; 0 &amp;&amp; groups[$route.params.idx] &amp;&amp; !groups[$route.params.idx].hidden"><div class="ui center aligned grid"><div class="ui column center aligned" ui-if="groups[$route.params.idx].n"><div class="ui raised segment center aligned" style="margin-top: 30px; max-width: 800px;"><h3 class="ui header">〈{{groups[$route.params.idx].n}}〉</h3><p class="center aligned">{{groups[$route.params.idx].intro}}<br class="thin-only"><a @click="toggleEdit($route.params.idx)" v-if="isMember(groups[$route.params.idx].idx)"><i class="edit icon"></i>{{edit ? $t('login.end') : ''}} {{ $t('group.edit_group') }}</a></p><router-link class="ui basic green button" to="/groups"><i class="globe icon"></i>{{$t('group.explore_groups')}}</router-link><div class="ui form" v-show="edit"><div class="field" v-if="!uid"><button class="ui orange button" @click="loginGoogle()"><i class="google icon"></i>{{$t('login.login_first')}}</button></div><div class="field"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_intro')}}</label><textarea v-model="newIntro" @input="filterInput('newIntro', $event)" :placeholder="$t('group.enter_intro_first')" rows="5" style="width: 100%; font-size: 18px;"></textarea></div></div><div class="field"><a class="ui green button" :class="{disabled: !newIntro}" @click="addIntro($route.params.idx)">{{$t('group.update_intro')}}</a></div></div><p v-if="(groups[$route.params.idx].members || []).length === 0"><button class="ui red button" @click="hideGroup($route.params.idx)"><i class="delete icon"></i>{{ $t('group.delete_group') }}</button></p><div class="ui grid center aligned"><div class="row"><p class="center aligned">{{$t('group.members')}}<span v-for="m in groups[$route.params.idx].members"><router-link :to="'/flag/' + m" v-if="isUser(m)"><img class="ui avatar" :src="users[m].photoURL" :alt="users[m].n" :title="users[m].n"></router-link></span><span v-if="uid"><a class="ui green tiny button" v-if="isUser(uid) &amp;&amp; !isMember(groups[$route.params.idx].idx)" @click="join(groups[$route.params.idx].idx)">{{$t('groups.join_group')}}</a><a class="ui red tiny button" v-if="isUser(uid) &amp;&amp; isMember(groups[$route.params.idx].idx)" @click="out(groups[$route.params.idx].idx)">{{$t('groups.out_group')}}</a></span></p></div><div class="two column stackable row"><div class="column"><div class="ui divided list center aligned" v-if="!edit"><div class="item center aligned">{{$t('group.resources_sorted')}}</div><div class="item center aligned" v-for="(r, index) in sortedResources" :key="index + r.n + r.href" v-show="!r.hidden"><div class="resource-content"><a :href="r.href" target="_blank" rel="noopener noreferrer"><img :src="'http://www.google.com/s2/favicons?domain=' + r.href" :alt="r.n">{{r.n}}</a><div class="filler"></div></div><div class="resource-buttons"><a class="ui blue button" v-if="isUser(uid)" @click="likeResource($route.params.idx, r.rid)"><i class="heart icon" v-if="(r.likes || []).includes(uid)"></i><i class="heart outline icon" v-else></i>{{(r.likes || []).length}} {{$t('group.likes')}}</a></div></div><div class="item ui form" v-show="uid"><div class="ui form" v-show="uid"><div class="field no-margin no-padding"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_resource')}}</label><input type="text" v-model="newResName" @input="filterInput('newResName', $event)" :placeholder="$t('group.enter_resource_first')"></div></div><div class="field no-margin no-padding"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_link')}}</label><input type="text" v-model="newHref" @input="filterInput('newHref', $event)" :placeholder="$t('group.enter_link_first')"></div></div><div class="field no-padding"><a class="ui green button" :class="{disabled: !newHref || !newResName}" @click="addRes($route.params.idx)">{{$t('group.add_resource')}}</a></div></div></div></div><div class="ui divided list center aligned" v-else><div class="item center aligned">{{$t('group.resources')}}</div><div class="item left aligned" v-for="(r, index) in groups[$route.params.idx].res" :key="index + r.n + r.href" v-show="!r.hidden"><!-- 編輯模式下顯示的輸入框--><div class="resource-content"></div><a @click="toggleEditResource(r, index)"><img :src="'http://www.google.com/s2/favicons?domain=' + r.href" :alt="r.n">{{r.n}}</a><div v-if="edit &amp;&amp; editResourceIndex === index"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_resource')}}</label><input type="text" v-model="tempResource.n" style="width: 100%; font-size: 20px; padding: 15px;"></div><div class="ui labeled input"><label class="ui label">{{$t('group.enter_link')}}</label><input type="text" v-model="tempResource.href" :placeholder="$t('group.enter_link_first')" style="width: 100%; font-size: 20px; padding: 15px;"></div><a class="ui green button" @click="saveResource($route.params.idx)">{{ $t('group.save') }}</a><a class="ui red button" @click="edit = false">{{ $t('group.cancel') }}</a></div></div></div></div><div class="column center aligned" v-show="!edit">{{ $t('login.leave_messages') }}<div class="ui divided list left aligned"><div class="item" v-for="(c, index) in latestChats" :key="index"><img class="ui avatar" :src="c.photoURL">{{removeBracket(c.n)}} : <VueMarkdownIt :source="c.t"></VueMarkdownIt></div><div class="item ui form" v-if="isMember(groups[$route.params.idx].idx)"><div class="field"><div class="ui labeled input"><img class="ui avatar" :src="photoURL"><textarea v-model="msg" @input="filterInput('msg', $event)" :placeholder="$t('group.anything_to_say')" rows="3" style="font-size: 18px; padding: 10px;"></textarea><a class="ui label green button" :class="{disabled: !msg}" @click="addChat($route.params.idx)">{{ $t('login.leave_messages') }}</a></div></div></div></div></div></div></div></div></div></div><!-- 新增分享區塊--><div class="ui center aligned grid" style="margin-top: 20px;"><div class="column"><div class="ui buttons"><button class="ui blue button" @click="copyLink"><i class="copy icon"></i>{{ $t('group.copy_link') }}</button><button class="ui facebook button" @click="shareToFacebook"><i class="facebook icon"></i>Facebook</button><button class="ui twitter button" @click="shareToTwitter"><i class="twitter icon"></i>Twitter</button><button class="ui line button" @click="shareToLine">Line</button></div></div></div></div></div></div>